import classes from "./team.module.css";
import { ReactComponent as Twitter } from "../../assets/socials/twitter-icon.svg";
// import Team1 from "../../assets/samples/team1.png";
// import Team2 from "../../assets/samples/team2.png";
import Team3 from "../../assets/samples/team3.png";
import Team4 from "../../assets/samples/team4.png";
// import Team5 from "../../assets/samples/team5.png";
import Team6 from "../../assets/samples/team6.png";
// import Team7 from "../../assets/samples/team7.png";
// import Team8 from "../../assets/samples/team8.png";
import Team9 from "../../assets/samples/team9.png";
import Team10 from "../../assets/samples/team10.png";
import Team11 from "../../assets/samples/team11.png";
import data from './teams.json';

  interface Teams {
    name: string;
    role: string;
    avatar: string;
    url: string;
  }

export default function Team() {

  let verified_teams: Teams[] = data;

  const avatars: any = {
    team1: Team9,
    team2: Team10,
    team3: Team3,
    team4: Team4,
    team5: Team11,
    team6: Team6
  }

  const onMouseMove = (e: any) => {
    const { clientX, clientY, currentTarget } = e;

    const { clientWidth, clientHeight, offsetLeft, offsetTop } = currentTarget;
    const threshold = 2;

    const horizontal = (clientX - offsetLeft) / clientWidth;
    const vertical = (clientY - offsetTop) / clientHeight;
    const rotateX = (threshold / 2 - horizontal * threshold).toFixed(2);
    const rotateY = (vertical * threshold - threshold / 2).toFixed(2);

    currentTarget.style.transform = `perspective(${clientWidth}px) rotateX(${rotateY}deg) rotateY(${rotateX}deg) scale3d(1, 1, 1)`;
  }

  const onMouseLeave = (e: any) => {
    const card = e.currentTarget;
    card.style.transform = `perspective(${e.currentTarget.clientWidth}px) rotateX(0deg) rotateY(0deg)`;
  }

  return (
    <div className={classes.teamContainer}>
      <h1>TEAM</h1>
      <div className={classes.teams}>
        {
          verified_teams.map((team, index) => (
            <div key={index} className={classes.team}>
              <img src={avatars[team.avatar]} className={classes.avatar} alt="sample" onMouseMove={onMouseMove} onMouseLeave={onMouseLeave}></img>
              <div className={classes.details}>
                <h2 className={classes.name}>{team.name}</h2>
                <p className={classes.role}>{team.role}</p>
                <a href={team.url} className={classes.url} target="_blank" rel="noreferrer">
                  <Twitter />
                </a>
              </div>
            </div>
          ))
        }
      </div>
    </div>
  );
}