import classes from "./nav.module.css";
import { Link as ScrollLink, animateScroll as scroll } from 'react-scroll';
import links from "./navLinks.json";
import React, { Fragment, useState } from "react";
import { useEffect } from "react";
import SideBar from "./sideBar";
import SocialBar from "../social/socialBar";
import { useWallet } from "@solana/wallet-adapter-react";
import { WalletMultiButton } from "@solana/wallet-adapter-react-ui";
import { shortenAddress } from "../../candy-machine";
import { Menu } from "@material-ui/icons";

interface Links {
  text: string;
  link: string;
}
function Nav({ selectedSchema, }: { selectedSchema: number }) {
  const [showBurger, shouldShowBurger] = useState<number>(10000);
  const [showSideBar, setShowSideBar] = useState<boolean>(false);
  const first_breakpoint: number = 1040;
  const second_breakpoint: number = 700;
  const [currentPage, setCurrentPage] = useState<string>("home");
  let verified_links: Links[] = links;
  const [isVisible, setIsVisible] = useState<boolean>(false);
  const wallet = useWallet();

  useEffect(() => {
    interface Resize {
      (e?: any): void;
    }
    const onResize: Resize = (e: any): void => {
      shouldShowBurger(window.innerWidth);
    };

    // toggle the visible class once the user scrolls past ~100px
    const onScroll = (e: any): void => {
      setIsVisible(window.scrollY > 100);
    }

    onResize();

    window.addEventListener("resize", onResize);
    window.addEventListener("scroll", onScroll);
    return () => {
      window.removeEventListener("resize", onResize);
      window.removeEventListener("scroll", onScroll);
    }
  }, []);

  const btn = (
    !wallet.connected ? (
      <button
        className={classes.connectWallet}
        onClick={() => {
          const walletBtn = document.querySelector(".btnConnect") as HTMLButtonElement;
          walletBtn.click();
        }}
      >
        Connect Wallet
      </button>
    ) : (
      <WalletMultiButton className={classes.connectWallet}>
        {shortenAddress(wallet.publicKey?.toBase58() || "")}
      </WalletMultiButton>
    )
  );

  const list_items = (
    <Fragment>
      {verified_links.map((link, index) => {
        return (
          <li key={index}>
            <ScrollLink
              activeClass={classes.selectedLink}
              to={link.text}
              offset={-100}
              spy={true}
              onSetActive={(e: any) => setCurrentPage(link.text)}
              className={currentPage === link.text ? classes.selectedLink : ""}
            >
              <span
                onClick={(e: any) => {
                  setCurrentPage(e.target.innerHTML);
                }}
              >
                {link.text}
              </span>
            </ScrollLink>
          </li>
        );
      })}
    </Fragment>
  );
  return (
    <ul className={`${classes.nav} ${isVisible && classes.visible}`}>
      <WalletMultiButton className={`${classes.connectWallet} btnConnect`} style={{ display: 'none' }}>df</WalletMultiButton>
        <div 
          className={classes.wrapImage}
          onClick={() => scroll.scrollTo(0, {duration: 0.1})}
        >
          <img
            src={process.env.PUBLIC_URL + `favicon.png`}
            alt="slideshow"
          />
        </div>

      <section className={classes.wrapText}>
        <SideBar
          classes={classes}
          screenSize={showBurger}
          breakpoints={{
            breakpoint1: first_breakpoint,
            breakpoint2: second_breakpoint,
          }}
          showSideBar={showSideBar}
        >
          {list_items}
          <SocialBar
            data={{
              discord: "https://discord.gg/oinkclubnft",
              telegram: "",
              twitter: "https://twitter.com/OinkClubNFT",
              instagram: "https://www.instagram.com/oinkclub/"
            }}
          />
        </SideBar>
        {showBurger <= first_breakpoint ? (
          <Fragment>
            {btn}
            <Menu
              onClick={() => {
                setShowSideBar((prev) => !prev);
              }}
              style={{fill: 'white', marginLeft: '20px'}}
            />
          </Fragment>
        ) : null}

        {showBurger >= first_breakpoint ? (
          <Fragment>
            {list_items}
            {btn}
            <SocialBar
              data={{
                discord: "https://discord.gg/oinkclubnft",
                telegram: "",
                twitter: "https://twitter.com/OinkClubNFT",
                instagram: "https://www.instagram.com/oinkclub/"
              }}
            />
          </Fragment>
        ) : null}

      </section>
    </ul>
  );
}
export default Nav;


