import data from './traits.json';
import classes from './rarities.module.css';
import Masonry from 'react-masonry-css';
import { KeyboardArrowDown } from '@material-ui/icons';
import React, { Fragment, useState } from 'react';

const Rarities = () => {

  const parts = Object.keys(data);

  const traits:any = data; 

  const breakpointColumnsObj = {
    default: 4,
    1200: 3,
    900: 2,
    540: 1
  };

  // which accordions are open/close
  const [accordions, setAccordions] = useState<boolean[]>([...new Array(parts.length).fill(false)]);

  const onClick = (e: React.MouseEvent, index: number) => {
    // reset all accordions
    let newAccordions = [...accordions].fill(false);
    // all are set to false if the user clicks an open accordion
    if (!accordions[index])
      newAccordions[index] = true;
    setAccordions(newAccordions);
  }

  return (
    <div className={classes.cheatSheetWrapper}>
      <div className={classes.traitsWrapper}>
        <div className={classes.rarity}>
          <div>Least Rare</div>
          <div className={classes.arrowContainer}>
            <div className={classes.arrow} />
            <KeyboardArrowDown className={classes.arrowHead} />
          </div>
          <div>Most Rare</div>
        </div>

        <Masonry
          breakpointCols={breakpointColumnsObj}
          className={classes.myMasonryGrid}
          columnClassName={classes.myMasonryGridColumn}
        >
          {parts.map((trait, index) => (
            <div
              key={index}
              className={`${classes.traitsContainer} ${
                accordions[index] && classes.accordionOpen
              } ${trait === "super-rare" && classes.superRare}`}
              onClick={(e) => {onClick(e, index)}}
            >
              <div className={classes.trait}>
                {trait}
                <KeyboardArrowDown 
                  className={`${classes.dropdownButton} ${accordions[index] && classes.active}`} 
                />
              </div>
              <ul>
                {traits[trait].map((item: string, index: number) => (
                  <li key={index} className={classes.item}>
                    {item.indexOf("equally") === -1 ? <Fragment>{item}</Fragment> : <i>{item}</i>}
                  </li>
                ))}
              </ul>
            </div>
          ))}
        </Masonry>
      </div>
    </div>
  );
}

export default Rarities;