import { KeyboardArrowDown } from "@material-ui/icons";
import { useState } from "react";
import classes from "./faq.module.css";
export default function FAQ() {
  const faqMap: string[][] = [
    [
      "How can I mint?",
      "Oinks are purchased through the big MINT button we have on the landing page. Minting will only be available to you if you have a compatible Solana Wallet. We recommend Phantom: https://phantom.app/ but there are a few others that work too - check out Sollet!"
    ],
    [
      "Can I mint on mobile?", 
      "The only wallet that works on IOS/Mobile is Sollet (https://www.sollet.io).",
      "If you're on android, you can use Sollet OR Phantom. To use phantom, download Kiwi Browser from the Play store and then add the Phantom extension & login. Then go to step 3 below and mint away!",
      "1. For ease, we’d recommend you create a Sollet wallet on your computer and send your $SOL to that wallet (as this will be the wallet you mint from).",
      "2. Download Chrome (or Brave) on IOS and head to the Oink website on one of these browsers (not safari).",
      "3. Click ‘Connect Wallet’ on the Oink Site and select Sollet...making sure to allow for pop ups. This will open Sollet in a new tab on your phone (keep this tab open). Login to your Sollet Wallet.",
      "4. Go back to the Oink Club Website tab and you should see your wallet is now connected ready to mint. If this is not the case, do step 3 again.",
      "5. If your wallet is now connected, hit the mint button and then go back to the Sollet tab to approve the mint...and congrats!",
    ],
    [
      "When is the drop and how much?", 
      "1.5 SOL & Friday 8th of October @ 5.30PM PDT."
    ],
    [
      "Is there a limit?", 
      "Yes and no. We will be limiting people to 1 Oink per transaction. However, you are able to return to the mint section as many times as you want though."
    ],
    [
      "How will I know if my Oink is rare?",
      "Rarity is incredibly important to us. We will be working with numerous third party tools like Rarity Tools & Rarity Sniper Discord to get our community automated rarity tools post launch.",
      "Higher rarity Oinks will be privy to higher rewards as we progress through the roadmap! Keep this in mind. However, In the meantime you can view the rarity cheat sheet on the site AT launch so you know what you’re looking at!"
    ],
    [
      "Will there be a secondary market?",
      "Yes, we will be listing on multiple platforms which include Solanart, DigitalEyes & MagicEden with verified collections so you can make sure you are trading ‘official’ Oinks.",
      "We apologise in advance if there are any issues around listings, as these platforms are still new. Rest assured we will be working around the clock to make sure you have a place to trade your Oinks!"
    ],
    [
      "Do I own my Oink?",
      "Yes, once you mint your Oink - that Oink is yours! Holding onto it may provide you access to additional rewards such as airdrops, discounts and more - so up to you! Alternatively, trade up with others so you can afford that higher ranked Oink you are chasing!",
    ]
  ];

  // which accordions are open/close
  const [accordions, setAccordions] = useState<boolean[]>([...new Array(faqMap.length).fill(false)]);

  const onClick = (e: React.MouseEvent, index: number) => {
    // reset all accordions
    let newAccordions = [...accordions].fill(false);
    // all are set to false if the user clicks an open accordion
    if (!accordions[index])
      newAccordions[index] = true;
    setAccordions(newAccordions);
  }

  const faqSection = (
    <div className={classes.container}>
      {faqMap.map((faqs, i) => {
        return (
          <ul
            className={`${classes.accordion} ${accordions[i] && classes.accordionOpen}`} 
            key={i}
            onClick={(e) => {onClick(e, i)}}
          >
            {faqs.map((faq, i)=>(
              i === 0 ? <span key={i} className={classes.question}><b>{faq}</b></span>
              : <li key={i} className={classes.answer}>{faq}</li>
            ))}
            <KeyboardArrowDown className={`${classes.dropdownButton} ${accordions[i] && classes.active}`}/>
          </ul>
        );
      })}
    </div>
  );

  return (
    <div>
      <h1>FREQUENTLY ASKED QUESTIONS</h1>
      <div>{faqSection}</div>
    </div>
  );
}
