import React from "react";
import { useRef } from "react";
import { useEffect } from "react";
import { useGeneralContext } from "../../context/generalContext";
export interface children {
  children?: React.ReactChild | React.ReactChild[];
}
interface SideBarChildren extends children {
  classes: { [key: string]: string };
  screenSize: number;
  breakpoints: {
    breakpoint1: number;
    breakpoint2: number;
  };
  showSideBar: boolean;
}
type Ref = undefined | any;
function SideBar({
  children,
  classes,
  screenSize,
  breakpoints,
  showSideBar,
}: SideBarChildren) {
  const sideBarCondition: boolean =
    screenSize > breakpoints.breakpoint1 || !showSideBar;
  const ref = useRef<Ref>();

  const { navHeight, setNavHeight } = useGeneralContext();

  useEffect(() => {
    if (navHeight === -1) {
      setNavHeight(0);
    }
  }, [navHeight, setNavHeight]);
  useEffect(() => {
    if (navHeight !== -1) {
      setNavHeight(
        parseInt(
          window.getComputedStyle(ref.current.parentElement).height
        ) as number
      );
    }
  }, [navHeight, screenSize, setNavHeight]);

  function getTop() {
    return navHeight < 0 ? 0 : navHeight;
  }

  //breakpoints expected to be in descending order

  return (
    <section
      className={`${classes.sideBar}`}
      ref={ref}
      style={{
        top: getTop(),
        paddingBottom: `calc(${getTop() * 1.5})`,
        overflow: 'auto',
        width: sideBarCondition ? 0 : "",
        padding: sideBarCondition ? 0 : "",
      }}
    >
      {children}
    </section>
  );
}
export default SideBar;
