import React, { Fragment, useState } from "react";
import classes from "./roadmap.module.css";
import Pig1 from "../../assets/gallery/gallery1.png";
import Pig2 from "../../assets/gallery/gallery2.png";
import Pig3 from "../../assets/gallery/gallery3.png";
import Pig4 from "../../assets/gallery/gallery4.png";
import { KeyboardArrowDown } from "@material-ui/icons";

export default function Roadmap() {

    const onMouseMove = (e: any) => {
        const { clientX, clientY, currentTarget } = e;

        const { clientWidth, clientHeight, offsetLeft, offsetTop } = currentTarget;
        const threshold = 2;

        const horizontal = (clientX - offsetLeft) / clientWidth;
        const vertical = (clientY - offsetTop) / clientHeight;
        const rotateX = (threshold / 2 - horizontal * threshold).toFixed(2);
        const rotateY = (vertical * threshold - threshold / 2).toFixed(2);

        currentTarget.style.transform = `perspective(${clientWidth}px) rotateX(${rotateY}deg) rotateY(${rotateX}deg) scale3d(1, 1, 1)`;
    }

    const onMouseLeave = (e: any) => {
        const card = e.currentTarget;
        card.style.transform = `perspective(${e.currentTarget.clientWidth}px) rotateX(0deg) rotateY(0deg)`;
    }

    const map: string[][] = [
        ["Step 1 - Farm Opening", "The Oinks have finally been let out, ready to get adopted!"],
        ["Step 2 - Sold Out!", "With great power comes great responsibility, so make sure you remember that while caring for your new Oink. Big things are coming!"],
        ["Step 3 - VIP Drop", "If you’re one of the few to have minted an Oink (or in possession of an Oink) with a VIP Chain, you will receive, via Airdrop, another rare oink free. Make sure to hold onto these Oinks, they’re valuable!"],
        ["Step 4 - Giveaways/Art Contests", "We’ll be holding some NFT contests and giveaways! Winners to be paid in both crypto and NFTs! We’ll also be opening some traits out for design by our community (if you know how to design) or make some suggestions in line with tokenomics"],
        ["Coming Soon: "],
        ["OC Charity Support", "The Oink community will come together to pick from a list of charities that we’d like to approach and work with! We will get involved with said charity in all aspects that we can (donations/merch/promotion of campaigns)."],
        ["Companions!", "We’re not going to drop companions too early and flood the market, instead we will listen to the Oink Community and when we’re ready (and want companions) we will bring them in.", "Our Amazing artist has already developed a few companion ideas for piglets for when we need them!"],
        ["Merch!", "Available on our website, you will also be able to buy Merch where we will drop exclusive OinkClub products such as hoodies, shirts, bags and other cool accessories. We will also be looking to partner with other projects to work on benefits that Oink holders will receive"],
        ["$Oink Tokenomics!", "We love Oinks, and we’d want to bring more value to the OC. We are currently investigating various ways we could implement a tokenomic system. There are a lot of use cases out there but we would like to bring a fresh take to the scene.", "There are many avenues that we could take but don't want to be the only voices! We would like to hear from you, our community, on what you think would be a good idea. A special channel will be opened in our discord where we will openly discuss various ideas and therefore see what the community would like.", "This may affect supply, it may not. One thing is for certain though is we do not want to damage our ecosystem in any way! (More on this to come)"],
        ["Oink On Chain Gaming", "With the foundation already created, the Oink Club will build an online world with fully functional on-chain combat (beta already in development!). In this world, you will be able to use your Oinks in combat with other players to level up. You'll also be able to earn in-game currency through on-chain combat and competitions as-well. We'll keep you updated!"]
    ];


    const roadmapText = (
        <div className={classes.roadmapText}>
            <p>Minting an Oink acts as your pass into the Oink Club, an incredibly intelligent unit of individuals working hard to achieve multiple goals across the metaverse. As an Oink Club member, you will have access to all that the OC has to offer both now and into the future.</p>
            <p>We have made it our goal to make sure that being a member of the Oink Club doesn’t mean just owning an Oink but more. The utility of the OC will be modest at first but will expand (rapidly) over time as we continue our development!</p>
            <p>There are a few milestones that we have listed below, but we wanted to provide as much transparency as possible so you will also be able to view our roadmap progress live too. </p>
        </div>
    )

    const roadmapBtn = (
      <div className={classes.roadmapBtnContainer}>
        <a
          href="https://trello.com/b/xXcvrZ0b/oinkclub-nft-roadmap"
          target="_blank"
          rel="noreferrer"
        >
          <button className={classes.roadmapBtn}>View Roadmap Progress</button>
        </a>
      </div>
    );

    // which accordions are open/close
    const [accordions, setAccordions] = useState<boolean[]>([...new Array(map.length).fill(false)]);

    const onClick = (e: React.MouseEvent, index: number) => {
        // reset all accordions
        let newAccordions = [...accordions].fill(false);
        // all are set to false if the user clicks an open accordion
        if (!accordions[index])
            newAccordions[index] = true;
        setAccordions(newAccordions);
    }

    return (
        <Fragment>
            <h1 className={classes.header} id="roadmap" >ROADMAP</h1>
            {roadmapText}
            {roadmapBtn}
            <div className={classes.roadmapContainer}>
                <div className={classes.roadmapLeft}>
                    {map.map((data, index) => (
                        (data.length > 1 ?
                            <ul
                                key={index}
                                className={`${classes.roadmapItem} ${accordions[index + map.length] && classes.roadmapItemOpen}`}
                                onClick={(e) => { onClick(e, index + map.length) }}
                            >
                                {data.map((value, index) => (
                                    index === 0 ? <span key={index} className={classes.roadmapTitle}>{value}</span>
                                        : <li className={classes.roadmapDescription}>{value}</li>
                                ))}
                                <KeyboardArrowDown className={`${classes.dropdownButton} ${accordions[index + map.length] && classes.active}`} />
                            </ul>
                        :
                            <ul
                                key={index}
                                style={{fontSize: '20pt', paddingLeft: '5px', fontWeight: 'bold'}}
                            >
                                {data.map((value, index) => (
                                    index === 0 ? <span key={index} className={classes.roadmapTitle}>{value}</span>
                                        : <li className={classes.roadmapDescription} style={{color: 'white'}}>{value}</li>
                                ))}
                            </ul>
                        )
                    ))}
                </div>
                <div className={classes.roadmapRight}>
                    <img src={Pig1} className={classes.sampleApe} alt="sample" onMouseMove={onMouseMove} onMouseLeave={onMouseLeave}></img>
                    <img src={Pig2} className={classes.sampleApe} alt="sample" onMouseMove={onMouseMove} onMouseLeave={onMouseLeave}></img>
                    <img src={Pig3} className={classes.sampleApe} alt="sample" onMouseMove={onMouseMove} onMouseLeave={onMouseLeave}></img>
                    <img src={Pig4} className={classes.sampleApe} alt="sample" onMouseMove={onMouseMove} onMouseLeave={onMouseLeave}></img>
                </div>
            </div>
        </Fragment>
    );
}