import classes from "./nav.module.css";
import { Fragment, useState } from "react";
import { useEffect } from "react";
import SocialBar from "../social/socialBar";
import { useWallet } from "@solana/wallet-adapter-react";
import { WalletMultiButton } from "@solana/wallet-adapter-react-ui";
import { shortenAddress } from "../../candy-machine";
import { Link } from "react-router-dom";

function Nav({
  selectedSchema,
}: {
  selectedSchema: number;
}) {
  const [showBurger, shouldShowBurger] = useState<number>(10000);
  const first_breakpoint: number = 1040;
  const [isVisible, setIsVisible] = useState<boolean>(false);
  const wallet = useWallet();

  useEffect(() => {
    interface Resize {
      (e?: any): void;
    }
    const onResize: Resize = (e: any): void => {
      shouldShowBurger(window.innerWidth);
    };

    // toggle the visible class once the user scrolls past ~100px
    const onScroll = (e: any): void => {
      setIsVisible(window.scrollY > 100);
    }

    onResize();

    window.addEventListener("resize", onResize);
    window.addEventListener("scroll", onScroll);
    return () => {
      window.removeEventListener("resize", onResize);
      window.removeEventListener("scroll", onScroll);
    }
  }, []);

  const btn = (
    !wallet.connected ? (
      <button
        className={classes.connectWallet}
        onClick={() => {
          const walletBtn = document.querySelector(".btnConnect") as HTMLButtonElement;
          walletBtn.click();
        }}
      >
        Connect Wallet
      </button>
    ) : (
      <WalletMultiButton className={classes.connectWallet}>
        {shortenAddress(wallet.publicKey?.toBase58() || "")}
      </WalletMultiButton>
    )
  );

  return (
    <ul className={`${classes.nav} ${isVisible && classes.visible}`}>
      <WalletMultiButton className={`${classes.connectWallet} btnConnect`} style={{ display: 'none' }}>df</WalletMultiButton>
      <Link to="/">
        <div className={classes.wrapImage}>
          <img
            src={process.env.PUBLIC_URL + `favicon.png`}
            alt="slideshow"
          />
        </div>
      </Link>

      <section className={classes.wrapText}>
        {showBurger >= first_breakpoint ? (
          <Fragment>
            {btn}
            <SocialBar
              data={{
                discord: "https://discord.gg/oinkclubnft",
                telegram: "",
                twitter: "https://twitter.com/OinkClubNFT",
                instagram: "https://www.instagram.com/oinkclub/"
              }}
            />
          </Fragment>
        ) : <Fragment>{btn}</Fragment>}

      </section>
    </ul>
  );
}
export default Nav;
