import { useState } from "react";
import { useContext, createContext } from "react";

import { children } from "../components/nav/sideBar";
type ContextType = {
  navHeight: number;
  setNavHeight(state: number): void;
};
const General = createContext<ContextType>({
  navHeight: 0,
  setNavHeight: (): void => {},
});

function GenaeralContext({ children }: children) {
  const [navHeight, setNavHeight] = useState(-1);

  const value: ContextType = { navHeight, setNavHeight };
  return <General.Provider value={value}>{children}</General.Provider>;
}

export function useGeneralContext() {
  return useContext(General);
}

export default GenaeralContext;
